<template>
  <div v-on:call_back="call">
    <!-- 
      specification mask
    -->

    <div v-if="spect" class="specification_mask"></div>
    <router-view v-if="$route.name=='shop_detail'"></router-view>

    <keep-alive v-if="$route.name=='shop'">
      <fatcomponent></fatcomponent>
    </keep-alive>
  </div>
</template>
<script>

import header1 from "./header";
import comment from "./comment";
import shopping from "./shopping";
import fatcomponent from "./fatcomponent";
export default {
  data() {
    return {
      index: 0,
      spect: null
    };
  },
  methods: {
    changecolor(index) {
      this.index = index;
      console.log(this.index);
    },
    call(spect) {
      console.log("你哈");
      this.spect = spect;
    }
  },
  components: {
    header1,
    comment,
    shopping,
    fatcomponent
  },
  beforeRouteUpdate(to,from,next){
    console.log('是是是是是是所所所所所所')
    console.log(to)
      console.log(from)
      next()
  },
  created(){
    console.log('现在有执行了吗........')
  }
};
</script>
<style scoped>
.specification_mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 400;
}
.body1 {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
}
.blue {
  color: #3190e8;
  border-bottom: 0.02rem solid #3190e8;
}
.none {
  color: black;

  border-bottom: 0px;
}
.option > div {
  flex: 1;
  text-align: center;
  padding-top: 0.15rem;
  padding-bottom: 0.15rem;
  box-sizing: border-box;
  font-weight: 1000;
  font-size: 0.15rem;
}
.option {
  display: -webkit-box;
  /* justify-content: space-between;
  flex-direction: row; */
  background: white;
}
</style>




