<template>
  <div>
    <div class="info_wrap animated fadeInRight faster">
      <title1></title1>
      <transition enter-active-class="animated bounceIn" name="bounce">
        <div v-if="show" class="window">
          <div>
            <div>!</div>
            <div>请在饿了吗App中打开</div>
            <input v-on:click="lp" type="button" value="确认">
          </div>
        </div>
      </transition>

      <div class="content">
        <div class="banner">
          <img src="https://cangdu.org/elm/static/img/activity.png" alt>
        </div>
        <div class="weixin">
          <div v-on:click="bounce">
            <img
              src="data:image/png;base64,"
            >
            <div>邀请微信好友</div>
          </div>
          <div v-on:click="bounce">
            <img
              src="data:image/png;base64,"
            >
            <div>邀请QQ好友</div>
          </div>

          <div v-on:click="bounce">
            <img
              src="data:image/png;base64,"
            >
            <div>面对新邀请</div>
          </div>
        </div>
        <div class="ll">
          <div>
            <p>累计收益</p>
            <p>
              <span class="shou">0</span>元
            </p>
          </div>
          <div>
            <p>成功邀请</p>
            <p>
              <span>0</span>人
            </p>
          </div>
        </div>

        <div class="footer">
          <div>
            <p>收益明细</p>
            <div>
              <img
                src="data:image/png;base64,"
              >
              <p>还不赶紧邀请好友</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import title1 from "./title";
export default {
  data() {
    return {
      show: false
    };
  },
  components: {
    title1
  },
  methods: {
    bounce() {
      this.show = true;
    },
    lp() {
      this.show = false;
    }
  }
};
</script>
<style scoped>
.info_wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 111;
  background: #f5f5f5;
}
.window > div > div:nth-child(1) {
  width: 0.6rem;
  height: 0.6rem;
  display: flex;
  font-size: 0.5rem;
  color: orange;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 0.03rem solid orange;
}
.window > div input {
  width: 100%;
  height: 20%;
  color: white;
  font-weight: 1000;
  background: #4cd964;
}
.window > div {
  width: 100%;
  height: 80%;
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.window {
  width: 80%;
  height: 30%;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -40%;
  margin-top: -20%;
  z-index: 1000;
  background: white;
  border-radius: 0.1rem;
}
.footer p {
  text-align: center;
}
.footer > div > div {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}
.footer img {
  width: 18%;
}
.footer {
  margin-top: 0.5rem;
  display: flex;
  height: 20%;
  background: rgb(245, 245, 245);
  width: 100%;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  left: 0;
}
.ll > div:nth-last-child(1) {
  border-left: 0.02rem solid gray;
}
.ll > div {
  box-sizing: border-box;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.ll {
  margin-top: 0.5rem;
  display: flex;
  justify-content: space-between;
}
.shou {
  color: orange;
}
.weixin {
  margin-top: 0.2rem;
  display: flex;
  height: 20%;
  justify-content: space-between;
  align-items: center;
  position: relative;
  background: white;
}
.weixin > div img {
  width: 35%;
}
.weixin > div {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  height: 60%;
  width: 33.3333333333333333333333333333333333333333333333333333333333333%;
}
.banner {
  height: 40%;
}
.banner > img {
  width: 100%;
  height: 100%;
}
.content {
  width: 100%;
  height: 90%;
  margin-top: 0.5rem;
  background: rgb(245, 245, 245);
  /* position: relative; */
}
</style>





