<template>
  <div>
    <transition name="router-slid" mode="out-in">
      <router-view :key="key" v-if="$route.path=='/vipcard/vipDescription'"></router-view>
    </transition>
    <transition name="router-slid" mode="out-in">
      <router-view :key="key" v-if="$route.path=='/vipcard/invoiceRecord'"></router-view>
    </transition>
    <transition name="router-slid" mode="out-in">
      <router-view :key="key" v-if="$route.path=='/vipcard/useCart'"></router-view>
    </transition>
    <div v-if="$route.path=='/vipcard'">
      <div class="header">
        <div>
          <div @click="back_()">
            <i class="el-icon-arrow-left"></i>
          </div>
          <div>会员中心</div>
        </div>
      </div>
      <div class="banner">
        <div>
          为账户为{{username}}
          <span>购买会员</span>
        </div>
      </div>
      <div v-on:click.stop="special" class="special">
        <div>
          <h3>会员特权</h3>
          <div>
            会员说明
            <span class="el-icon-arrow-right"></span>
          </div>
        </div>
      </div>
      <div class="free">
        <div>
          <div class="middle">
            <div class="img">
              <img
                src="data:image/jpeg;base64,"
                alt
              >
            </div>

            <div class="ko">
              <h4>减免配送免费</h4>
              <p>每月减免30单,每月减免39单,每单最高减4元</p>
              <p>蜂鸟送专免</p>
            </div>
          </div>
        </div>
      </div>

      <div class="free">
        <div>
          <div class="middle">
            <div class="img">
              <img
                src="data:image/jpeg;base64,"
                alt
              >
            </div>

            <div class="ko">
              <h4>减免配送免费</h4>
              <p>每月减免30单,每月减免39单,每单最高减4元</p>
              <p>蜂鸟送专免</p>
            </div>
          </div>
        </div>
      </div>
      <div class="kai2">
        <div class="kai">
          <div>开通会员</div>
          <div>
            <div>
              1个月
              <span>¥20</span>
            </div>
            <div class="buy">购买</div>
          </div>
        </div>
      </div>
      <div v-on:click="one" class="card">
        <div>
          <div>兑换会员</div>
          <div>
            使用卡号加密
            <span class="el-icon-arrow-right"></span>
          </div>
        </div>
      </div>
      <div v-on:click.stop="record" class="fapiao">
        <div>
          <div>购买记录</div>
          <div>开发票</div>
        </div>
      </div>
      <div class="footer"></div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      username: "急急急"
    };
  },
  computed: {
    key() {
      return this.$route.path + Math.random();
    }
  },
  methods: {
    back_() {
      this.$router.go(-1);
    },
    special() {
      this.$router.push({
        //useCard1
        name: "vipDescription",
        params: {
          name: "我的优惠",
          username: this.username
        }
      });
    },
    record() {
      this.$router.push({
        //useCard1
        name: "invoiceRecord",
        params: {
          name: "购买记录",
          username: this.username
        }
      });
    },
    one() {
      this.$router.push({
        //useCard1
        name: "useCard",
        params: {
          name: "兑换会员",
          username: this.username
        }
      });
    }
  },
  created() {
    console.log(this.$route);
  }
};
</script>
<style scoped>
.footer {
  width: 100%;
  height: 10%;
  background: rgb(245, 245, 245);
  position: fixed;
  bottom: 0;
  left: 0;
}
.fapiao > div {
  width: 90%;
  margin: 0 auto;
  height: 0.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.fapiao {
  width: 100%;
  height: 0.4rem;
}
.card > div {
  width: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card {
  width: 100%;
  height: 0.4rem;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 0.3rem solid rgb(245, 245, 245);
}
.buy {
  color: orangered;
  backface-visibility: white;
  width: 0.5rem;
  height: 0.3rem;
  border: 1px solid orangered;
  border-radius: 0.02rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.kai2 {
  border-bottom: 0.3rem solid rgb(245, 245, 245);
}
.kai > div:nth-child(2) span {
  color: orangered;
}
.kai > div:nth-child(2) {
  height: 0.53rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.kai > div:nth-child(1) {
  height: 0.53rem;
  display: flex;
  align-items: center;
}
.kai {
  width: 100%;
  height: 1.2rem;
  padding: 0 0.2rem;
  margin: auto;
  background: #fff;
}
.middle {
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
}
.ko {
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.img {
  height: 100%;
}
.free p {
  font-size: 0.12rem;
  color: gray;
}
.free > div img {
  float: left;
  height: 0.3rem;
  margin-right: 0.2rem;
}
.free > div {
  width: 90%;
  margin: auto;
  height: 100%;
  display: flex;
  align-items: center;
  border-bottom: 0.03rem solid rgb(245, 245, 245);
}
.free {
  width: 100%;
  height: 1rem;
  background: #fff;
}
.special > div {
  width: 90%;
  margin: auto;
  height: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 0.03rem solid rgb(245, 245, 245);
}
.special {
  width: 100%;
  height: 0.5rem;
  display: flex;
  align-items: center;
}
.banner span {
  color: white;
  background: gray;
}
.banner > div {
  width: 50%;
  margin-left: 0.2rem;
}
.banner {
  height: 0.5rem;
  background: rgb(245, 245, 245);
  display: flex;
  align-items: center;
}
.header {
  width: 100%;
  background: #3190e8;
  height: 0.5rem;
  display: flex;
  align-items: center;
}
.header > div {
  width: 50%;
  margin-left: 0.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header div {
  color: white;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
}

.router-slid-enter-active,
.router-slid-leave-active {
  transition: all 0.4s;
}
.router-slid-enter,
.router-slid-leave-active {
  transform: translate(100%);
  opacity: 0;
}
</style>


